import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CalculatorService {
  
  counter: number = 0;

  calculatorSubject: Subject<number> = new Subject(); // Publisher
  calculatorObservable: Observable<number> = this.calculatorSubject.asObservable(); // Subscriber
  
  value: number = 0;

  constructor() { }

  incrementCounter(){
        this.counter++;
       // console.log(this.counter);
  }
  
  updateValue(buttonValue: string){
    this.value = ( this.value*10 ) + Number(buttonValue);
    this.calculatorSubject.next(this.value);
    console.log(this.value);
  }

//  calback are used to handle result of async functions in javascript
// this is not the only way
// 1. callback - simplest
// 2. promises - essentially callback but with a different pattern/syntax
// 3. async / await : different syntax for same thing
// 4. Rxjs > Much more feature packed
// pub-sub concept- publication & subscription concept. 
// ex whatsapp grp - 1person writes msg on a grp to 10 other people in the grp
// 1 publisher & 10 subscriber in that grp
// Rxjs - unlimited no of publisher & subscriber
// result of async function will be published & all subscriber will get the update

}
